.link-button {
  background-color: transparent;
  border: none;
  cursor: pointer;
  text-decoration: underline;
  display: inline;
  margin: 0;
  padding: 0;
}

#loadingOverlay {
  display: table;
  width: 100%;
  height: 100%;
  position: fixed;
  top: 0;
  left: 0;
  background-color: rgba(66, 66, 66, 0.5);
  z-index: 10000;
  text-align: center;
}

#loadingOverlay > h1 {
  display: table-cell;
  vertical-align: middle;
}

#loadingOverlay > h1 > span {
  padding: 20px;
  background-color: #FFFFFF;
}